<template>
	<div class="app-container">
		<el-alert title="注意:处理过程约耗时0~5分钟，请稍后查询" show-icon type="warning">
		</el-alert>
		<inputs-header :options="options" :isAdmin="true" @change='changeCompany' @btnClick="checkIccid" class="clearfix"></inputs-header>
		<el-table :data="tableData" style="width: 100%" border v-loading="isLoad" >
			<el-table-column type="expand">
				<template scope="props">
					<el-form label-position="left" class="demo-table-expand">
						<el-form-item class="flow-details" v-for="(item, $index) in props.row.info" :keys="$index" :label="item.OFFER_NAME">
							<el-progress :text-inside="true" :stroke-width="14" :percentage="getPercent(item.CUMULATION_TOTAL, item.CUMULATION_LEFT)"></el-progress>
							<div style="float:left;font-size: 13px;color: #999;margin: 0;">已用: {{parseInt(parseInt(item.CUMULATION_TOTAL - item.CUMULATION_LEFT)/1024)}}MB/{{parseInt(parseInt(item.CUMULATION_TOTAL)/1024)}}MB
							</div>
							<div style="float:right;font-size: 11px;color: #f88b00;">
								有效期至{{item.END_TIME.substring(0,4)}}年{{item.END_TIME.substring(4,6)}}月{{item.END_TIME.substring(6,8)}}日
							</div>
						</el-form-item>
					</el-form>
				</template>
			</el-table-column>
			<el-table-column label="接入号码" prop="number">
			</el-table-column>
			<el-table-column label="ICCID" prop="iccid">
			</el-table-column>
			<el-table-column label="已用总量(MB)" prop="alreadyTraffic">
			</el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
	</div>
</template>

<script>
import InputsHeader from '../../../components/InputsHeader/index';
import {
	getListIotCardInfo,
	getIotCardInfoByIccid
} from '../../../api/userInfo';
import {
	getListManufacturerGeneral,
	getListIotCardByMatch,
	giveManufacturerAuthority,
	getAndGive
} from '../../../api/admin/usual';

import { backCN } from 'utils/index';
export default {
	data() {
		return {
			tableData: [],
			tableCount: 0,
			currentPage: 1,
			value: '',
			iccidInput: '',
			iccidArr: [],
			options:[],
			isLoad: true
		}
	},
	watch: {
	},
	mounted() {
		this.initHeader()
	},
	methods: {
		initHeader() {
			this.isLoad = true
			getListManufacturerGeneral()
				.then(res => {
					res.msg === 0 ? this.options = res.mList : this.$message.error(backCN(res.code))
					res.msg === 0 ? '' : this.Load = false
				}).catch(err => {
					this.isLoad = false
					this.$message.error(backCN(err.status))
				})
		},
		getPercent(total, left) {
			var percent = parseInt((parseInt(total / 1024) - parseInt(left / 1024)) / parseInt(total / 1024));
			return parseInt((parseInt(total / 1024) - parseInt(left / 1024)) / parseInt(total / 1024)) * 100;
		},
		changeCompany(val) {
			this.isLoad = true
			giveManufacturerAuthority({
				mid: val
			}).then(res => {
				if (res.msg == 0) {
					this.ListIotCardInfo(1);
				} else {
					this.isLoad = false
					this.$message.error(backCN(res.code));
				}
			}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});
		},
		pageChange(val) {
			this.ListIotCardInfo(val);
		},
		checkIccid(iccid) {
			if (iccid) {
				this.IotCardInfoByIccid(iccid);
			} else {
				this.ListIotCardInfo(1);
			}
		},
		IotCardInfoByIccid(iccid) {
			this.isLoad = true
			getIotCardInfoByIccid({
				iccid: iccid
			})
				.then(res => {
					this.isLoad = false; 
					if (res.msg === 0) {
						res.iccid = iccid;
						this.tableData = [res];
						this.tableCount = 1;
					} else {
						this.tableData = [];
						this.tableCount = 0;
						res.msg != 1 ? this.$message.error(backCN(res.code)) : ''
					}
				}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});
		},
		ListIotCardInfo(page) {
			this.isLoad = true
			getListIotCardInfo({
				page: page
			}).then(res => {
				this.isLoad = false
				if (res.msg == 0) {
					this.tableData = res.infoList;
					this.tableCount = res.count;
				} else {
					this.tableData = [];
					this.tableCount = 0;
					res.msg != 1 ? this.$message.error('查询失败:'+backCN(res.code)) : ''
				}
			}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN('查询失败:'+err.status))
			})
		}
	},
	components: {
		InputsHeader
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}

.clearfix:before,
.clearfix:after {
	display: table;
	content: "";
}

.clearfix:after {
	clear: both
}

.box-card {
	width: 100%;
	min-width: 637px;
	margin-bottom: 20px;
}

.survey-container {}

.survey-item-wrap {
	padding: 5px 0;
}

.survey-item {
	border-right: 1px solid #e0e0e0;
}

.survey-item-wrap:last-child .survey-item {
	border: none;
}

.survey-item h3 {
	font-size: 13px;
	color: #666;
}

.survey-item p {
	color: #f88b00;
	font-size: 25px;
}

.content-rowspan div {
	padding-left: 13px;
	line-height: 46px;
	border-bottom: 1px solid #ECEDEE;
}

.extend .cell {
	padding: 0;
}

.flow-details {
	margin: 0;
	line-height: 0;
	border-bottom: 1px solid #e8e8e8;
}

.flow-details:last-child {
	border: none;
}
</style>


