import fetch from 'utils/fetch';
export function getListIotCardInfo(data) {
  return fetch({
    url: '/recharge/manager/listIotCardInfo.do',
    method: 'get',
    data
  });
}

// 通过iccid查流量详情
export function getIotCardInfoByIccid(data) {
  return fetch({
//	 参数  iccid=8986031540252024112
    url: '/recharge/manager/getIotCardInfoByIccid.do',
    method: 'get',
    data
  });
}


